<template>
  <main class="content container orders__container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'orders' }">
            Список заказов
          </router-link>
        </li>
      </ul>

      <h1 class="content__title" @click="test">
        Список заказов
      </h1>
      <span class="content__info">
        {{ orders?.length }} заказов
      </span>
    </div>

    <section class="cart">
      <div class="cart__field">
        <ul class="cart__list orders__list">
          <OrdersItem v-for="item in orders" :key="item.id" :item="item"/>
        </ul>
      </div>
    </section>
  </main>
</template>

<script>
  import axios from 'axios';
  import { API_BASE_URL } from '@/config';
  import OrdersItem from '@/components/OrdersItem';

  export default {
    components: { OrdersItem },
    data: function() {
      return {
        orders: this.ordersData,
      }
    },
    methods: {
      test: function() {
        console.log(this.orders);
      },
      ordersData: function() {
        let ordersData = [];
        if (!this.ordersId) {
          return [];
        }
        this.ordersId.forEach(id => {
          axios
            .get(API_BASE_URL+'/api/orders/'+id, {
              params: {
                userAccessKey: this.$store.state.userAccessKey
              }
            })
            .then(response => {
              ordersData.push(response.data);
            });
        })
        return ordersData;
      },
    },
    computed: {
      ordersId: function() {
        return JSON.parse(localStorage.getItem('orders'));
      },
    },
    created: function() {
      this.orders = this.ordersData();
    },
  }
</script>