<template>
  <div class="product__counter form__counter">
    <button type="button" aria-label="Убрать один товар" @click.prevent="minusAmount">
      <svg width="10" height="10" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" v-model.number="amount" name="count">

    <button type="button" aria-label="Добавить один товар" @click.prevent="plusAmount">
      <svg width="10" height="10" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<script>
  export default {
    props: [ 'item', ],
    computed: {
      amount: {
        get() {
          return this.item.amount;
        },
        set(value) {
          this.$store.dispatch('updateCartProductAmount', { productId: this.item.id, amount: value });
        }
      },
    },
    methods: {
      plusAmount: function() {
        this.$store.dispatch('updateCartProductAmount', { productId: this.item.id, amount: this.item.amount+1 });
      },
      minusAmount: function() {
        this.$store.dispatch('updateCartProductAmount', { productId: this.item.id, amount: this.item.amount-1 });
      },
    },
  }
</script>