<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'order' }">
            Оформление заказа
          </router-link>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ productsAmount($store.state.cartProducts) }} товаров
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText v-model="formData.name" title="ФИО" :error="formError.name" type="text" name="name" placeholder="Введите Ваше полное имя"/>

            <BaseFormText v-model="formData.address" title="Адрес" :error="formError.address" type="text" name="address" placeholder="Введите Ваш адрес"/>

            <BaseFormText v-model="formData.phone" title="Телефон" :error="formError.phone" type="tel" name="phone" placeholder="Введите Ваш телефон"/>

            <BaseFormText v-model="formData.email" title="Email" :error="formError.email" type="email" name="email" placeholder="Введите Ваш Email"/>

            <BaseFormTextarea v-model="formData.comment" title="Комментарий" :error="formError.comment" name="comment" placeholder="Ваши пожелания"/>
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" v-model="deliveryValue" value="0">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" v-model="deliveryValue" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash" checked>
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <OrderCartOrders v-for="product in cartProducts" :key="product.id" :title="product.title" :amount="product.amount" :price="product.price" :id="product.id"/>
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>{{ deliveryValue || 0 }} ₽</b></p>
            <p>Итого: <b>{{ productsAmount($store.state.cartProducts) }}</b> товаров на сумму <b>{{ totalPrice | numberFormat }} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
  import axios from 'axios';
  import { API_BASE_URL } from '@/config';
  import OrderCartOrders from '@/components/OrderCartOrders';
  import BaseFormText from '@/components/BaseFormText';
  import BaseFormTextarea from '@/components/BaseFormTextarea';
  import BaseFormField from '@/components/BaseFormField';
  import productsAmount from '@/helpers/productsAmount';
  import { mapGetters } from 'vuex';
  import numberFormat from '@/helpers/numberFormat';

  export default {
    components: { BaseFormText, BaseFormTextarea, BaseFormField, OrderCartOrders },
    data: function() {
      return {
        formData: {},
        formError: {},
        formErrorMessage: '',
        deliveryValue: 0,
      }
    },
    computed: {
      cartProducts: function() {
        return this.$store.state.cartProducts;
      },
      ...mapGetters({ totalPrice: 'cartTotalPrice' }),
    },
    methods: {
      order: function() {
        this.formError = {};
        this.formErrorMessage = '';


        axios
          .post(API_BASE_URL+'/api/orders', {
            ...this.formData,
          },{
            params: {
              userAccessKey: this.$store.state.userAccessKey
            }
          })
          .then(response => {
            this.$store.commit('resetCart');
            this.$store.commit('updateOrderInfo', response.data);
            this.uploadOrders(response.data);
            this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
          })
          .catch(error => {
            console.log(error);
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage = error.response.data.error.message;
          });
      },
      uploadOrders: function(data) {
        let orders = JSON.parse(localStorage.getItem('orders')) || [];
        orders.push(data.id);
        localStorage.setItem('orders', JSON.stringify(orders));
      },
      productsAmount,
    },
    filters: {
      numberFormat
    }
  }
</script>