import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],

    userAccessKey: null,

    cartProductsData: [],

    orderInfo: null,
  },
  mutations: {
    updateOrderInfo: function(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart: function(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    deleteCartProduct: function(state, productId) {
      state.cartProducts = state.cartProducts.filter(item => item.productId !== productId);
    },
    updateCartProductAmount: function(state, { productId, amount }) {
      const item = state.cartProducts.find(item => item.id === productId);

      if(item) {
        item.amount = amount;
      }
      state
    },

    updateUserAccessKey: function(state, key) {
      state.userAccessKey = key;
    },
    updateCartProductsData: function(state, items) {
      state.cartProductsData = items;
    },
    syncCartProducts: function(state) {
      state.cartProducts = state.cartProductsData.map(item => {
        return {
          ...item.product,
          amount: item.quantity,
          image: item.product.image.file.url
        }
      });
    }
  },
  getters: {
    // cartDetailProducts: function(state) {
    //   return state.cartProducts.map(item => {
    //     const productData = state.cartProductsData.find(p => p.product.id === item.id);
    //     const product = productData.product;

    //     return {
    //       ...product,
    //       amount: productData.quantity,
    //       image: product.image.file.url,
    //     };
    //   });
    // },
    cartTotalPrice: function(state) {
      return state.cartProducts.reduce((acc,item) => (item.price * item.amount) + acc, 0);
    }
  },
  actions: {
    loadOrderInfo: function(context, orderId) {
      return axios
        .get(API_BASE_URL+'/api/orders/'+orderId, {
          params: {
            userAccessKey: context.state.userAccessKey
          }
        })
        .then(response => {
          context.commit('updateOrderInfo', response.data)
        });
    },
    loadCart: function(context) {
      return axios.get(API_BASE_URL+'/api/baskets', {
        params: {
          userAccessKey: context.state.userAccessKey,
        }
      })
      .then(response => {
        if(!context.state.userAccessKey) {
          localStorage.setItem('userAccessKey', response.data.user.accessKey);
          context.commit('updateUserAccessKey', response.data.user.accessKey);
        }
        context.commit('updateCartProductsData', response.data.items);
        context.commit('syncCartProducts');
      });
    },
    addProductToCart: function(context, { productId, amount }) {
      return axios
        .post(API_BASE_URL+'/api/baskets/products', {
          productId,
          quantity: amount,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey
          }
        })
        .then(response => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        });
    },
    deleteCartProduct: function(context, productId) {
      return axios
        .delete(API_BASE_URL+'/api/baskets/products',{
          params: {
            userAccessKey: context.state.userAccessKey,
          },
          data: {
            productId,
          }
        })
        .then(response => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        });
    },
    updateCartProductAmount: function(context, { productId, amount }) {
      context.commit('updateCartProductAmount', { productId, amount });

      if(amount < 1) {
        return;
      }

      return axios
        .put(API_BASE_URL+'/api/baskets/products', {
          productId,
          quantity: amount,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey
          }
        })
        .then(response => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
  }
});