<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        {{ products.length }} товар
      </span>
    </div>

    <div class="content__catalog">

      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategoryId" :color.sync="filterColor" />

      <section class="catalog" style="position:relative">
        <img v-if="productsLoading" width="64px" height="64px" style="position:absolute; top:50%; left:50%; transform:translate(-50%,-50%)" src="img/preloader.gif">
        <span v-if="productsLoadingFailed" style="font-weight:500;font-size:20px;text-align:center;">Произошла ошибка при загрузке товаров(</span>
        <button v-if="productsLoadingFailed" @click.prevent="loadProducts" style="background:none; border:none;margin:0 auto; margin-top: 10px; width:40px; height:40px;cursor: pointer;">
          <img src="img/restart.png">
        </button>

        <ProductList v-if="!productsLoading" :products="products"/>

        <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" />
      </section>

    </div>
  </main>
</template>

<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import ProductList from '@/components/ProductList.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import BasePagination from '@/components/BasePagination.vue';

export default {
  components: { ProductList, ProductFilter, BasePagination },
  data: function () {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColor: 0,

      page: 1,
      productsPerPage: 6,

      productsData: null,
      loadProductsTimer: null,
      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    products: function () {
      return this.productsData
      ? this.productsData.items.map(product => {
        return {
          ...product,
          image: product.image.file.url,
        }
      })
      : [];
    },
    countProducts: function () {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    loadProducts: function() {
      const timeLoad = 0;

      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout(this.loadProductsTimer);
      this.loadProductsTimer = setTimeout(() => {
        axios.get(API_BASE_URL+`/api/products`, {
          params: {
            page: this.page,
            limit: this.productsPerPage,
            categoryId: this.filterCategoryId,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
          }
        })
        .then(response => this.productsData = response.data)
        .catch(() => this.productsLoadingFailed = true)
        .then(() => this.productsLoading = false);
      }, timeLoad);
    }
  },
  created: function() {
    this.loadProducts();
  },
  watch: {
    page: function() {
      this.loadProducts();
    },
    filterCategoryId: function() {
      this.loadProducts();
    },
    filterPriceFrom: function() {
      this.loadProducts();
    },
    filterPriceTo: function() {
      this.loadProducts();
    },
  },
  mounted: function() {
    scrollTo(0,0);
  },
};
</script>